import React, {Component} from "react";
import Button from "../../components/UI/Button/Button"
import Input from "../../components/UI/Input/Input";
import classes from "./Auth.css";
import * as actions from "../../store/actions/index";
import {connect} from "react-redux";
import Spinner from "../../components/UI/Spinner/Spinner";
import {Redirect} from "react-router-dom";
class Auth extends Component{
    state = {
        control: {
            name: {
                elementType: "input",
                elementConfig: {
                    type: "text",
                    placeholder: "your Name",
                },
                value: "",
                validation: {
                    required: true,
                    minLength: true,
                    email: true,
                },
                valid: false,
                touched: false,
            },
            password: {
                elementType: "input",
                elementConfig: {
                    type: "text",
                    placeholder: "your password",
                },
                value: "",
                validation: {
                    required: true,
                    maxLength: true,
                    minLength: true,
                },
                valid: false,
                touched: false,
            },
        },
        isSignup: true
    };

    componentDidMount() {
        if(this.state.buildingBurger === false && this.props.authRedirectPath !== "/"){
            this.props.onSetAuthRedirectPath();
        }
    }

    static checkValidity(value, rules){
        let isValid = true;
        if(rules.required){
            isValid = value.trim() !== "" && isValid;
        }
        if(rules.maxLength){
            isValid = value.length < 16 && isValid;
        }
        if(rules.minLength){
            isValid = value.length > 5 && isValid;
        }
        if(rules.email){
            isValid = value.match(/(^[a-zA-Z1-9][a-zA-Z0-9]{5,16})(@qq.com)|(@gmail.com)|(@163.com)/) !== null  && isValid;
        }
        console.log(isValid);
        return isValid;

    }

    inputChangedHandler = (event, controlName) => {

        const updatedControls = {
            ...this.state.control,
            [controlName]: {
                ...this.state.control[controlName],
                value: event.target.value,
                valid: Auth.checkValidity(event.target.value, this.state.control[controlName].validation),
                touched: true,
            }
        };

        this.setState({
            control: updatedControls
        })
    };

    submitHandler = (event) => {
        event.preventDefault();
        this.props.onAuth(this.state.control.name.value, this.state.control.password.value, this.state.isSignup)
    };

    switchAuthModeHandler = () => {
        this.setState(prevState => {
            return {
                isSignup: !prevState.isSignup
            };
        })
    };

    render(){
        const formElementsArray = [];
        for(let index in this.state.control){
            formElementsArray.push({
                id: index,
                config: this.state.control[index]
            })
        }

        let form = formElementsArray.map(formElement => {
            return (
                <Input
                    key={formElement.id}
                    elementType={formElement.config.elementType}
                    elementConfig={formElement.config.elementConfig}
                    value={formElement.config.value}
                    valid={formElement.config.valid}

                    touched={formElement.config.touched}
                    changed={(event) => this.inputChangedHandler(event, formElement.id)}
                />

            )
        });

        if(this.props.loading) {
            form = <Spinner/>;
        }

        let errorMessage = null;

        if(this.props.error) {
            errorMessage = (
                <p>{this.props.error.message}</p>
            );
        }

        let authRedirect = null;
        if(this.props.isAuthenticated) {
            authRedirect = <Redirect to={this.props.authRedirectPath}/>
        }

        return(
            <div className={classes.Auth}>
                {authRedirect}
                {errorMessage}
                <form onSubmit={this.submitHandler}>
                    {form}
                    <Button btnType="Success">SUBMIT</Button>
                </form>
                <Button
                    clicked={this.switchAuthModeHandler}
                    btnType="Danger">switch to {this.state.isSignup? "signin": "signup"}</Button>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        loading: state.auth.loading,
        error: state.auth.error,
        isAuthenticated: state.auth.token !== null,
        buildingBurger: state.burgerBuilder.building,
        authRedirectPath: state.auth.authRedirectPath
    }
};

const mapDispatchToProps = dispatch => {
    return {
        onAuth: (name, password, isSignup) => dispatch(actions.auth(name, password, isSignup)),
        onSetAuthRedirectPath: () => dispatch(actions.setAuthRedirectPath("/")),
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(Auth);


