import React, {Component} from "react";
import { connect } from "react-redux";
import Aux from "../../hoc/Auxes";
import Burger from "../../components/Burger/Burger";
import BuildControls from "../../components/Burger/BuildControls/BuildControls";
import Modal from "../../components/UI/Modal/Modal";
import OrderSummary from "../../components/Burger/OrderSummary/OrderSummary";
// import axios from "../../axios-orders";
import Spinner from "../../components/UI/Spinner/Spinner";
// import withErrorHandler from "../../hoc/withErrorHandler/withErrorHandler";
// import * as actionTypes from "../../store/actions/actionTypes";
import * as actions from "../../store/actions/index";

class BurgerBuilder extends Component{
    state = {
        // ingredients: null,
        // ingredients: {
        //     salad: 0,
        //     bacon: 0,
        //     cheese: 0 ,
        //     meat: 0,
        // },
        // totalPrice: 2,
        // purchasable: false,
        purchasing: false,  // 隐藏或者打开模态框
        loading: false,  // display spinner
    };

    componentDidMount(){
        // console.log(this.props);
        // axios.get("/ingredients.json")
        //     .then(response => {
        //             this.setState({
        //                 ingredients: response.data
        //             });
        //
        //     })
        this.props.onInitIngredients();
    }



    updatePurchaseState = (updatedIngredients) => {
        const sum = Object.keys(updatedIngredients).map((item) => {
            return updatedIngredients[item]
        }).reduce((sum, cur) => {
            return sum + cur
        });
        return sum > 0;
    };

    // addIngredientHandler = (type) => {
    //     const oldCount = this.state.ingredients[type];
    //     const updatedCount = oldCount + 1;
    //     const updatedIngredients = {
    //         ...this.state.ingredients
    //     };
    //     updatedIngredients[type] = updatedCount;
    //     const priceAddition = INGREDIENT_PRICE[type];
    //     const oldPrice = this.state.totalPrice;
    //     const newPrice = oldPrice + priceAddition;
    //     this.setState({
    //         totalPrice: newPrice,
    //         ingredients: updatedIngredients,
    //     });
    //     this.updatePurchaseState(updatedIngredients);
    // };
    //
    // removeIngredientHandler = (type) => {
    //     const oldCount = this.state.ingredients[type];
    //     if(oldCount < 1){
    //         return;
    //     }
    //     const updatedCount = oldCount - 1;
    //     const updatedIngredients = {
    //         ...this.state.ingredients
    //     };
    //     updatedIngredients[type] = updatedCount;
    //     const priceDeduction = INGREDIENT_PRICE[type];
    //     const oldPrice = this.state.totalPrice;
    //     const newPrice = oldPrice - priceDeduction;
    //     this.setState({
    //         totalPrice: newPrice,
    //         ingredients: updatedIngredients,
    //     });
    //     this.updatePurchaseState(updatedIngredients);
    // };

    purchaseHandle = () => {
        if(this.props.isAuthenticated){
            this.setState({
                purchasing: true
            })
        }else {
            this.props.onSetRedirectPath("/checkout");
            this.props.history.push("/auth");
        }
    };

    purchaseCancelHandler = () => {
        this.setState({
            purchasing: false
        });
    };

    //仅仅负责URL的跳转，跳转到Checkout组件
    purchaseContinueHandler = () => {

        // const queryParams = [];
        // for(let index in this.state.ingredients){
        //     queryParams.push(encodeURIComponent(index) + '=' + encodeURIComponent(this.state.ingredients[index]));
        // }
        // queryParams.push("price=" + this.state.totalPrice);
        // const queryString = queryParams.join('&');
        // this.props.history.push({
        //     pathname: '/checkout',
        //     search: "?" + queryString
        // });
        this.props.onInitPurchase();
        this.props.history.push('/checkout');
    };

    render(){
        const disabledInfo = {
            ...this.props.ings
        };
        for(let index in disabledInfo){
            disabledInfo[index] = disabledInfo[index] < 1
        }
        let orderSummary = null;
        //在没收到ingredients信息之前，burger的相关组件一起先变为spinner
        let burger = <Spinner/>;
        if(this.props.ings){
            burger = (
                <Aux>
                    <Burger ingredients={this.props.ings}/>
                    <BuildControls
                        ingredientAdded={this.props.onIngredientAdded}
                        ingredientRemoved={this.props.onIngredientRemoved}
                        disabled={disabledInfo}
                        price={this.props.price}
                        purchasable={this.updatePurchaseState(this.props.ings)}//计算总共价格是否大于0来判断是否能提交订单
                        ordered={this.purchaseHandle}
                        isAuth={this.props.isAuthenticated}
                    />
                </Aux>
            );
            //模态框的内容，
            orderSummary = <OrderSummary
                ingredients={this.props.ings}
                price={this.props.price}
                purchaseCancelled={this.purchaseCancelHandler}//将purchaseContinueHandler方法给orderSummary执行
                purchaseContinued={this.purchaseContinueHandler}//orderSummary执行此方法就会是purchasing为false
            />;
        }
        if(this.state.loading){
            orderSummary = <Spinner/>;

        }
        //当purchasing为false就会触发Modal的show为false
        return (
            <Aux>
                <Modal show={this.state.purchasing} modalClosed={this.purchaseCancelHandler}>
                    {orderSummary}
                </Modal>
                {burger}
            </Aux>
        );
    }
}

const mapStateToProps = state => {
    return {
        ings: state.burgerBuilder.ingredients,
        price: state.burgerBuilder.totalPrice,
        isAuthenticated: state.auth.token !== null
    };
};

const mapDispatchToProps = dispatch => {
    return {
        onIngredientAdded: (ingName) => dispatch(actions.addIngredients(ingName)),
        onIngredientRemoved: (ingName) => dispatch(actions.removeIngredients(ingName)),
        onInitIngredients: () => dispatch(actions.initIngredients()),
        onInitPurchase: () => dispatch(actions.purchaseInit()),
        onSetRedirectPath: (path) => dispatch(actions.setAuthRedirectPath(path)),
    }
};

// export default withErrorHandler(BurgerBuilder, axios);
export default connect(mapStateToProps, mapDispatchToProps)(BurgerBuilder);


