import React, {Component} from "react";
import {connect} from "react-redux";
import Button from "../../../components/UI/Button/Button";
import Spinner from "../../../components/UI/Spinner/Spinner";
import classes from "./ContactData.css";
// import axios from "../../../axios-orders";
import Input from "../../../components/UI/Input/Input";
import * as actions from "../../../store/actions/order";

class ContactData extends Component{
    state = {
        orderForm: {
            name: {
                elementType: "input",
                elementConfig: {
                    type: "text",
                    placeholder: "your Name",
                },
                value: "",
                validation: {
                    required: true,
                    maxLength: true,
                },
                valid: false,
                touched: false,
            },
            street: {
                elementType: "input",
                elementConfig: {
                    type: "text",
                    placeholder: "your Street",
                },
                value: "",
                validation: {
                    required: true
                },
                valid: false,
                touched: false,
            },
            email: {
                elementType: "input",
                elementConfig: {
                    type: "text",
                    placeholder: "your E-mail",
                },
                value: "",
                validation: {
                    required: true,
                    email: true,
                },
                valid: false,
                touched: false,
            },
            deliveryMethod: {
                elementType: "select",
                elementConfig: {
                    options: [
                        {value: "fastest", displayValue: "Fastest"},
                        {value: "cheapest", displayValue: "cheapest"}
                    ]
                },
                value: "faster",
                validation: {

                },
                valid: true,
                touched: false,
            },
        },
        // loading: false,  //作为是否将Form变为spinner的flag
        formValid: false,
    };
    //提交Form需要执行的方法
    orderHandler = (event) => {
        event.preventDefault();//阻止页面刷新
        //启动spinngerUI组件
        // this.setState({
        //     loading: true
        // });
        const formData = {};//为了把State.orderForm对象变为数组
        //遍历orderForm对象的属性，把新对象FormData的属性设置为orderForm对象的属性，值为orderForm的属性值，
        //name属性的值为string，可直接赋值，对象FormData的属性为每个input的名字，值为输入的值
        for(let index in this.state.orderForm){
            formData[index] = this.state.orderForm[index].value;
        }
        //要提交给数据库的order对象
        const order = {
            ingredients: this.props.ings,
            price: this.props.price,
            orderData: formData,
            userId: this.props.userId,
        };
        // axios.post("/orders.json ", order)
        //     .then(response => {
        //         //获得数据就把spinnerUI组件的flag设置为false，并回到主界面
        //         this.setState({
        //             loading: false,
        //         });
        //         this.props.history.push("/");
        //     })
        //     .catch(error => {
        //         this.setState({
        //             loading: false,
        //         })
        //     });
        this.props.onOrderBurger(order, this.props.token);
    };
    //处理键盘事件触发的input，检测输入值的合法性，
    inputChangedHandler = (event, inputIdentifier ) => {
        const updatedOrderForm = {...this.state.orderForm};
        const updatedFormElement = {...updatedOrderForm[inputIdentifier]};
        updatedFormElement.value = event.target.value;
        // 第一次触发当前这个input，利用touched这个flag标明，
        updatedFormElement.touched = true;
        //用valid这个flag判断当前input的参数合法性，
        updatedFormElement.valid = ContactData.checkValidity(updatedFormElement.value, updatedFormElement.validation);
        //将更新结果传递给State.orderForm的复制版updatedOrderForm
        updatedOrderForm[inputIdentifier] = updatedFormElement;
        let flag = true;
        //判断整个Form的valid是否合法，遍历updatedOrderForm，当遇到false，flag就永久变为false，
        for(let index in updatedOrderForm){
            flag = updatedOrderForm[index].valid && flag;
            // if(flag === this.state.orderForm[index].valid){
            //     break;
            // }else {
            //     this.setState({
            //         formInValid: false
            //     });
            // }
            // console.log(updatedOrderForm[index].valid)
        }
        // 触发键盘事件就会更新orderForm，updatedOrderForm的valid决定FormValid的合法性
        this.setState({
            orderForm: updatedOrderForm,
            formValid: flag,
        })
    };
    //接受输入值，根据该input具有的规则，判断输入值是否合法，一个规则都没有返回该值合法
    static checkValidity(value, rules){
        let isValid = true;
        if(rules.required){
            isValid = value.trim() !== "" && isValid;
        }
        if(rules.maxLength){
            isValid = value.length < 8 && isValid;
        }
        if(rules.email){
            isValid = value.match(/(^[a-zA-Z1-9][a-zA-Z0-9]{5,16})(@qq.com)|(@gmail.com)|(@163.com)/) !== null  && isValid;
        }
        console.log(isValid);
        return isValid;

    }

    render(){
        //将state.orderForm对象转变为数组，方便对其进行遍历操作
        const formElementsArray = [];
        for(let index in this.state.orderForm){
            formElementsArray.push({
                id: index,
                config: this.state.orderForm[index],
            });
        }

        //一开始设置form为Form
        let form = (
            <form>
                {formElementsArray.map(item => {
                    return (
                        //Input组件接受
                        <Input
                            key={item.id}//独一无二的key
                            elementType={item.config.elementType}//input类型
                            elementConfig={item.config.elementConfig}//input的placeHolder
                            value={item.config.value}
                            valid={item.config.valid}
                            touched={item.config.touched}
                            changed={(event) => this.inputChangedHandler(event, item.id)}
                        />
                    )
                })}
                {/*<Input inputtype="input" type="email" name="email" placeholder="your email"/>*/}
                {/*<Input inputtype="input" type="text" name="Postal" placeholder="your Postal Code"/>*/}
                <Button
                    disabled={!this.state.formValid}//当form不合法的时候设置该button的disabled为true
                    btnType="Success"
                    clicked={this.orderHandler} //填写完信息把orderHandler作为Button的点击函数
                >Order</Button>
            </form>
        );

        if(this.props.loading){
            form = <Spinner/>;
        }
        return (
            <div className={classes.ContactData}>
                <h4>Enter your Contact Data</h4>
                {form}
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        ings: state.burgerBuilder.ingredients,
        price: state.burgerBuilder.totalPrice,
        loading: state.order.loading,
        token: state.auth.token,
        userId: state.auth.userId
    }
};

const mapDispatchToProps = dispatch => {
    return {
        onOrderBurger: (orderData, token) => dispatch(actions.purchaseBurger(orderData, token))
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(ContactData);


